<template>
  <div class="page">
    <div class="page-error" v-if="this.error == true">
      <ErrorPage />
    </div>
    <div class="page-show" v-else>
      <Banner
        titleBanner="Semua Halaman"
        subtitleBanner="Home / Semua Halaman"
        image="night-sky-wallpaper-thumb.jpg"
      />
      <div class="p-10">
        <div class="container mx-auto max-w-5xl">
          <div
            class="wrapper grid grid-cols-1 gap-5 mt-10 lg:max-w-5xl mx-auto max-w-full container"
          >
            <div
              v-if="this.pageList.length > 0"
              class="card lg:flex block w-full gap-5 p-3 border-b border-secondary"
              v-for="item in pageList[page - 1]"
              :key="item.id"
            >
              <div class="flex-auto border border-secondary">
                <img
                  :src="baseIMG(item.icon)"
                  class="lg:w-56 object-cover w-full"
                />
                <!-- <img src="https://backend.primbonjawa.net/pages/box-review.jpg" alt=""> -->
              </div>
              <div class="flex-auto w-full lg:mt-0 mt-5">
                <a
                  :href="
                    $router.resolve({
                      path: `/${item.page_type}`,
                    }).href
                  "
                >
                  <h2 class="text-3xl mt-0 font-bluunext text-white">
                    {{ item.title }}
                  </h2>
                </a>
                <!-- <p
                  class="mt-3 font-nunito text-gray-400"
                  v-if="item.short_description.length < 200"
                  v-html="item.short_description"
                ></p>
                <p
                  class="mt-3 font-nunito text-gray-400"
                  v-if="item.short_description.length >= 200"
                  v-html="item.short_description.substring(0, 200) + '...'"
                ></p> -->
                <p class="mt-3 font-nunito text-gray-400">
                  {{ item.short_description }}
                </p>
              </div>
            </div>
            <div class="loading font-nunito text-gray-400" v-else>
              <p>Memuat...</p>
            </div>
            <div class="mt-5">
              <pagination
                :total-pages="totalPages"
                :total="total"
                :per-page="perPage"
                :current-page="currentPage"
                :has-more-pages="hasMorePages"
                @pagechanged="showMore"
              >
              </pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import ErrorPage from "@/components/neterror";
  import Pagination from "@/components/Pagination";
  import axios from "axios";
  import TestimoniView from "@/components/Testimoni";
  import Banner from "@/components/Banner";
  export default {
    data() {
      return {
        searchSlug: "",
        pageList: [],
        page: 1,
        totalPages: 4,
        total: 40,
        perPage: 4,
        currentPage: 1,
        hasMorePages: true,
        error: false,
      };
    },
    components: {
      TestimoniView,
      Banner,
      Pagination,
      ErrorPage,
    },
    methods: {
      showMore(page) {
        this.page = page;
        this.currentPage = page;
      },
      chunk(arr, size) {
        return Array.from({ length: Math.ceil(arr.length / size) }, (v, i) =>
          arr.slice(i * size, i * size + size)
        );
      },
    },
    async mounted() {
      document
        .querySelector('meta[name="description"]')
        .setAttribute("content", "Menampilkan semua page");
      try {
        axios
          .get(`${this.$baseURL}pages`)
          .then((response) => {
            this.pageList = this.chunk(response.data.data, this.perPage);
            this.totalPages = this.pageList.length;
            this.error = false;
          })
          .catch((error) => {
            this.error = true;
          });
      } catch (e) {
        alert(e);
      }
    },
  };
</script>

<style></style>
