<template>
  <header
    class="w-full box-border top-0 lg:relative fixed z-30 header border-secondary border-b bg-black text-white"
  >
    <div class="md:container lg:max-w-5xl max-w-full box-border mx-auto">
      <div class="flex text-center w-full justify-between p-3 mx-auto">
        <div
          class="w-full lg:block hidden flex-auto text-gray-400 lg:items-center lg:w-auto font-prozalibre font-medium text-xs"
        >
          <div class="mt-3">
            <router-link
              :to="item.link"
              class="ml-5 tracking-wide hover:text-white"
              v-for="item in menuLeft"
              :key="item.name"
              >{{ item.name }}</router-link
            >
          </div>
        </div>
        <div
          class="lg:max-w-2xl max-w-full box-border lg:flex-none flex-auto font-bluunext items-center"
        >
          <h1 class="lg:text-3xl text-xl lg:text-center text-left text-primary">
            Primbon.
          </h1>
        </div>
        <div
          class="w-full lg:block hidden flex-auto text-gray-400 lg:items-center lg:w-auto font-prozalibre font-medium text-xs"
        >
          <div class="mt-3 lg:inline-block hidden">
            <router-link
              :to="item.link"
              class="tracking-wide hover:text-white"
              :class="{
                'ml-5': item.right,
                'ml-0': !item.right,
              }"
              v-for="item in menuRight"
              :key="item.name"
              >{{ item.name }}</router-link
            >
          </div>
        </div>
        <div class="lg:hidden">
          <button
            @click="openNav"
            class="flex items-center px-3 py-2 border rounded text-primary border-primary hover:text-white hover:border-white"
          >
            <svg
              class="fill-current h-3 w-3"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <title>Menu</title>
              <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
            </svg>
          </button>
        </div>
      </div>
    </div>
    <transition name="slide-fade" mode="out-in">
      <div
        class="mobile-menu p-3 top-0 z-40 border-l-2 border-secondary lg:hidden block bg-black w-full h-screen fixed box-border"
        v-show="toggleNav"
      >
        <button class="float-right mr-2 p-3 mt-1 text-2xl" @click="closeNav">
          <Icon icon="la:times" />
        </button>
        <router-link to="/" exact>
          <h2 class="text-primary text-xl mt-0 font-bluunext">Primbon.</h2>
        </router-link>
        <div
          class="mt-5 container mx-auto grid font-bold grid-cols-1 text-gray-400 gap-2 font-bluunext text-lg uppercase"
        >
          <router-link
            :to="item.link"
            class="tracking-wide hover:text-black uppercase p-2 hover:bg-primary"
            v-for="item in menuLeft"
            :key="item.name"
            >{{ item.name }}</router-link
          >
          <router-link
            :to="item.link"
            class="tracking-wide hover:text-black uppercase p-2 hover:bg-primary"
            v-for="item in menuRight"
            :key="item.name"
            >{{ item.name }}</router-link
          >
        </div>
      </div>
    </transition>
  </header>
</template>

<script>
  import axios from "axios";
  import { Icon } from "@iconify/vue";
  export default {
    components: {
      Icon,
    },
    methods: {
      openNav: function () {
        this.toggleNav = true;
        document.body.style.overflow = "hidden";
      },
      closeNav: function () {
        this.toggleNav = false;
        document.body.style.overflow = "auto";
      },
    },
    watch: {
      $route: function () {
        this.toggleNav = false;
        document.body.style.overflow = "auto";
        axios
          .get(`${this.$baseURL}page/cek-weton`)
          .then((result) => {
            var dataWeton = result.data.data;
            var aliasWeton = "Cek Weton";
            this.menuLeft[2].name = aliasWeton.toUpperCase();
            this.menuLeft[2].link = `/cek-weton`;
          })
          .catch((err) => {});
        axios
          .get(`${this.$baseURL}page/kalender-jawa`)
          .then((result) => {
            var dataKalender = result.data.data;
            var aliasKalender = "Kalender";
            this.menuRight[0].name = aliasKalender.toUpperCase();
            this.menuRight[0].link = `/kalender-jawa`;
          })
          .catch((err) => {});
        axios
          .get(`${this.$baseURL}page/translate-jawa`)
          .then((result) => {
            var dataTranslate = result.data.data;
            var aliasTranslate = "Translate";
            this.menuRight[1].name = aliasTranslate.toUpperCase();
            this.menuRight[1].link = `/translate-jawa`;
          })
          .catch((err) => {});
      },
    },
    data() {
      return {
        toggleNav: false,
        titleWeton: "",
        slugWeton: "",
        menuLeft: [
          {
            name: "HOME",
            link: "/",
          },
          {
            name: "HALAMAN",
            link: "/pages",
            right: true,
          },
          {
            name: "",
            link: "",
          },
        ],
        menuRight: [
          {
            name: "",
            link: "",
            right: false,
          },
          {
            name: "",
            link: "",
            right: true,
          },

          {
            name: "HUBUNGI",
            link: "/contact",
            right: true,
          },
        ],
      };
    },
    mounted() {
      axios
        .get(`${this.$baseURL}page/cek-weton`)
        .then((result) => {
          var dataWeton = result.data.data;
          var aliasWeton = "Cek Weton";
          this.menuLeft[2].name = aliasWeton.toUpperCase();
          this.menuLeft[2].link = "/cek-weton";
        })
        .catch((err) => {});
      axios
        .get(`${this.$baseURL}page/kalender-jawa`)
        .then((result) => {
          var dataKalender = result.data.data;
          var aliasKalender = "Kalender";
          this.menuRight[0].name = aliasKalender.toUpperCase();
          this.menuRight[0].link = "/kalender-jawa";
        })
        .catch((err) => {});
      axios
        .get(`${this.$baseURL}page/translate-jawa`)
        .then((result) => {
          var dataTranslate = result.data.data;
          var aliasTranslate = "Translate";
          this.menuRight[1].name = aliasTranslate.toUpperCase();
          this.menuRight[1].link = "/translate-jawa";
        })
        .catch((err) => {});
    },
  };
</script>
