<template>
  <div>
    <Banner
      titleBanner="Hubungi kami"
      subtitleBanner="primbonjawa.net/contact"
      image="night-sky-wallpaper-thumb.jpg"
    />

    <div class="max-w-5xl mt-20 mx-auto p-5 box-border">
      <div class="grid grid-cols-3 gap-10">
        <div class="card p-10 border border-secondary text-center">
          <img
            :src="iconsAssets('icons8-country-80.png')"
            class="mx-auto"
            alt="icon8 primbonjawa "
          />
          <div class="body-card space-y-7 mt-5">
            <div class="title-card">
              <h2 class="text-2xl font-bluunext text-primary">Alamat</h2>
            </div>
            <div class="content-card font-nunito">
              <p class="text-sm text-gray-200">Kab. Bondowoso, Dabasah 02</p>
              <p class="text-sm text-gray-200">Indonesia, Jawatimur</p>
            </div>
          </div>
        </div>
        <div class="card p-10 border border-secondary text-center">
          <img
            :src="iconsAssets('icons8-shake-phone-80.png')"
            class="mx-auto"
            alt="icon8 primbonjawa "
          />
          <div class="body-card space-y-7 mt-5">
            <div class="title-card">
              <h2 class="text-2xl font-bluunext text-primary">No. Telepon</h2>
            </div>
            <div class="content-card font-nunito">
              <p class="text-sm text-gray-200">+62 000-000-00</p>
              <p class="text-sm text-gray-200">+62 000-000-00</p>
            </div>
          </div>
        </div>
        <div class="card p-10 border border-secondary text-center">
          <img
            :src="iconsAssets('icons8-send-email-80.png')"
            class="mx-auto"
            alt="icon8 primbonjawa "
          />
          <div class="body-card space-y-7 mt-5">
            <div class="title-card">
              <h2 class="text-2xl font-bluunext text-primary">Email</h2>
            </div>
            <div class="content-card font-nunito">
              <p class="text-sm text-gray-200">info@primbonjawa.net</p>
              <p class="text-sm text-gray-200">jobs@primbonjawa.net</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from "axios";
  import Banner from "@/components/Banner";

  export default {
    data() {
      return {
        title: "",
        subtitle: "",
        descriptions: "",
        slug: "",
      };
    },
    // -7.9233024, 113.8262016

    components: {
      Banner,
    },
    mounted() {
      document
        .querySelector('meta[name="description"]')
        .setAttribute("content", this.descriptions);
      try {
        axios
          .get(`${this.$baseURL}pages${this.$route.fullPath}`)
          .then((response) => {
            var query = response.data.data;
            this.icon =
              ` https://backend.primbonjawa.net/favicon/` + query.icon ?? "";
            this.page_type = "Halaman / " + query.page_type;
            this.title = query.title;
            this.subtitle = query.subtitle;
            this.slug = query.slug;
            this.descriptions = query.description;
            document.title = query.title + " - Halaman Primbon";
            document.description = query.short_description;
          })

          .catch((error) => {
            console.log(error);
            this.error = true;
          });
      } catch (e) {
        alert(e);
      }
    },
  };
</script>
